
a:hover,
a:focus {
  color: #23527c;
  text-decoration: none;
}

text, h1, h2, h3, h4, p {
    font-family: Arial, myriad-pro,"Myriad Pro","Open Sans",sans-serif;
}

.welcome {
    background-image: url(banner.jpg);
    background-size: cover;
    padding: 2% auto 3%;
    margin-top: 10px;
    height: 250px;
    padding-left: 70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


#headerwelcome {
    color: white;
    margin: 0;
}

#whatsnew {
    background-color: inherit;
    color: white;
    border: 1px solid white;
    font-size: 100%;
    margin-top: 20px;
}

.buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 40px;
}

.headers {
    margin: 20px;
}

.app {
    margin: auto 15% 5%;
}

.card:hover,
.card:focus, 
#whatsnew:hover {
    transform: scale(1.05) perspective(1px);
    box-shadow: 2px 3px 5px 0 rgb(0 0 0 / 15%);
    cursor: pointer;
}
